(function() {
  'use strict';

  angular
    .module('pulld')
    .run(runBlock);

  /** @ngInject */
  function runBlock($state, $rootScope) {

      var eventSuccess = $rootScope.$on('$stateChangeSuccess', function () {
      if($state.is('homepage')) {
        $rootScope.isHomepage = true;
      } else {
        $rootScope.isHomepage = false;
      }
    });

    $rootScope.$on('$destroy', eventSuccess);
  }

})();

(function() {
  'use strict';

  angular
    .module('pulld')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider, $locationProvider) {
    $stateProvider
      .state('homepage', {
        url: '/',
        template: '<pulld-homepage></pulld-homepage>',
        data: {
          meta: {
            title: 'Pull\'d',
            description: ''
          }
        }
    }).state('about', {
        url: '/about-us',
        template: '<pulld-about></pulld-about>',
        data: {
          meta: {
            title: 'About Us',
            description: ''
          }
        }
      }).state('menu', {
        url: '/menu',
        template: '<pulld-menu></pulld-menu>',
        data: {
          meta: {
            title: 'Menu',
            description: ''
          }
        }
      }).state('order', {
        url: '/order',
        template: '<pulld-order></pulld-order>',
        data: {
          meta: {
            title: 'Order online',
            description: ''
          }
        }
      }).state('partnerships', {
        url: '/partnerships',
        template: '<pulld-partnerships></pulld-order>',
        data: {
          meta: {
            title: 'Partnerships',
            description: ''
          }
        }
    }).state('contact', {
        url: '/contact',
        template: '<pulld-contact></pulld-contact>',
        data: {
          meta: {
            title: 'Contact us',
            description: ''
          }
        }
      });

    $urlRouterProvider.otherwise('/');
    $locationProvider.html5Mode(true);
  }

})();

